/** @jsx jsx */
import { jsx } from "theme-ui";
import { connectRefinementList } from "react-instantsearch-dom";
import { rem } from "@src/theme";
import * as _ from "lodash";
import { useState } from "react";
const RefinementList = ({ items, currentRefinement, refine }) => {
  const [active, setActive] = useState("All");
  const refinements = [
    "Insight",
    "Statistic",
    "Report",
    "Academic article",
    "Infographic",
    "Tool",
    "Video",
  ];

  return (
    <ul
      sx={{
        display: "flex",
        flexWrap: ["nowrap", null, "wrap"],
        fontSize: [rem(18), null, rem(18)],
        li: {
          fontWeight: 600,
          mr: [rem(20), null, rem(30)],
          whiteSpace: "nowrap",
          "&:last-child": {
            pr: "4%",
          },
        },
        overflowX: "scroll",
        "&::-webkit-scrollbar": {
          display: "none",
        },
        "-ms-overflow-style": ["none"],
        " scrollbar-width": ["none"],
        a: {
          display: "inline-block",
          borderBottom: "2px solid transparent",
          "&:hover": {
            borderBottomColor: "inherit",
          },
        },
      }}
    >
      <li key="all">
        <a
          href="#"
          sx={{
            color: active === "All" ? "csiDarkGrey" : "#616161",
            fontWeight: active === "All" ? "bold" : "",
            borderBottom:
              active === "All" ? "2px solid #616161!important" : "none",
          }}
          onClick={(event) => {
            event.preventDefault();
            setActive("All");
            refine("");
          }}
        >
          All
        </a>
      </li>

      {refinements.map((item) => (
        <li key={item}>
          <a
            href="#"
            sx={{
              fontWeight: active === item ? "bold" : "",
              borderBottom:
                active === item ? "2px solid #616161!important" : "none",
              color: active === item ? "csiDarkGrey" : "#616161",
            }}
            onClick={(event) => {
              event.preventDefault();

              refine(item);
              setActive(item);
            }}
          >
            {item}
          </a>
        </li>
      ))}
    </ul>
  );
};

const CustomRefinementList = connectRefinementList(RefinementList);

export default CustomRefinementList;
