/** @jsx jsx */
import { jsx } from "theme-ui";
import algoliasearch from "algoliasearch/lite";
import {
  InstantSearch,
  Hits,
  RefinementList,
  connectStateResults,
} from "react-instantsearch-dom";
import { rem } from "@src/theme";
import { Link } from "gatsby";
import TypeRefinementList from "@components/TypeRefinementList";
import Container from "@components/Container";
import ScrollContainer from "@components/ScrollContainer";
import ResourceCard from "@components/ResourceCard";
import EyeOff from "@src/components/icons/eye";
import CollectionHeader from "@src/components/CollectionHeader";
import Layout from "@src/components/Layout";
const searchClient = algoliasearch(
  "ED9HZMT8Z4",
  "65441d4adde5fd0d03a0b683d232262a"
);

interface ComponentProps {
  defaultRefinement?: string[];
  refinementType?: string;
  defaultQuery?: string;
  isSearch: Boolean;
  heading: string;
  description?: string;
}
export default function({
  defaultRefinement,
  isSearch,
  heading,
  description,
  refinementType,
  defaultQuery,
}: ComponentProps) {
  return (
    <Layout sticky={true} title={heading}>
      <InstantSearch
        searchClient={searchClient}
        indexName={process.env.ALGOLIA_INDEX || "resources-csi-01-prod"}
      >
        <CollectionHeader
          defaultQuery={defaultQuery}
          title={heading}
          description={description}
          isSearch={isSearch}
          bgType={isSearch ? "search" : "user"}
        />

        <div
          sx={{
            py: [rem(30), null, rem(50)],
          }}
        >
          <ScrollContainer>
            <TypeRefinementList attribute="type" operator="and" />
          </ScrollContainer>
        </div>

        {refinementType && (
          <div
            sx={{
              display: "none",
            }}
          >
            <RefinementList
              attribute={refinementType}
              defaultRefinement={defaultRefinement}
            />
            <RefinementList attribute="enabled" defaultRefinement={[true]} />
          </div>
        )}

        <Container>
          <Results>
            <section
              sx={{
                ".ais-Hits-list": {
                  display: "grid",
                  gridTemplateColumns: [
                    "repeat(1,1fr)",
                    "repeat(2,1fr)",
                    "repeat(3,1fr)",
                  ],
                  gridColumnGap: ["0.85rem", null, "3.00rem"],
                  gridRowGap: ["0.85rem", null, "3.00rem"],
                },
                pb: rem(80),
              }}
            >
              <Hits hitComponent={ResourceCard} />
            </section>
          </Results>
        </Container>
      </InstantSearch>
    </Layout>
  );
}

const Results = connectStateResults(
  ({ searchResults, children, searchState }) => {
    return searchResults && searchResults.nbHits !== 0 ? (
      children
    ) : !searchResults && !Object.keys(searchState).length ? null : (
      <section
        sx={{
          textAlign: "center",
          pb: rem(40),
        }}
      >
        <EyeOff />
        <h1
          sx={{
            mt: rem(10),
            mb: rem(10),
            fontSize: [rem(20), null, rem(25)],
          }}
        >
          We can't SEE any matching evidence
        </h1>
        <p
          sx={{
            mt: 0,
          }}
        >
          Try{" "}
          <Link
            to="/"
            sx={{
              textDecoration: "underline",
              fontWeight: 600,
            }}
          >
            searching
          </Link>{" "}
          for other research or insights.
        </p>
      </section>
    );
  }
);
